<template>
<div>
    <el-row class='user_box container'>
        <el-col>
            <el-card class='register-form'>
                <div slot='header'>
                    <h3>
                        <span>注册帐号</span>
                        <el-button class='float-rt' type='text'><a id='index' href='#/login'>返回登录页面</a></el-button>
                    </h3>
                </div>
                <el-form :model='newuser' :rules='rules' ref='regform'>
                    <el-form-item prop='tel'>
                        <el-input type='text' v-model.trim='newuser.tel' placeholder='请输入11位手机号' suffix-icon="iconfont aibd-icon-user"></el-input>
                    </el-form-item>
                    <el-form-item prop='code'>
                        <el-input type='text' v-model.trim='newuser.code' placeholder='请输入验证码'>
                                <template slot="append">
                                    <el-button type='primary' @click.native="loginCode" v-text="codeinner" :disabled="codedis"></el-button>
                                </template>
                            </el-input>
                    </el-form-item>
                    <el-form-item prop='userid'>
                        <el-input type='text' v-model.trim='newuser.userid' placeholder='请输入用户昵称'></el-input>
                    </el-form-item>
                    <el-form-item prop='pass'>
                        <input-password :value.sync='newuser.pass' placeholder='请输入密码,密码为字母、数字和符号两种及以上的组合6-16个字符'></input-password>
                    </el-form-item>
                    <el-form-item prop='password'>
                        <input-password :value.sync='newuser.password' placeholder='再次输入密码' @input.native="inputres"></input-password>
                    </el-form-item>
                    <el-form-item>
                        <el-col :span='10' :push='1'>
                            <el-button type='primary' class='btn-block' @click.native="submits">注册帐号</el-button>
                        </el-col>
                        <el-col :span='10' :push='2'>
                            <el-button type='default' class='btn-block' @click.native="utils.formreset($refs['regform'])">重置</el-button>
                        </el-col>
                    </el-form-item>
                </el-form>
            </el-card>
        </el-col>
    </el-row>
</div>
</template>

<script>
export default {
    data() {
        let vm = this;
        return {
            newuser: {
                tel: '',
                code: '',
                userid: '',
                pass: '',
                password: ''
            },
            rules: {
                tel: [{
                        message: '手机号不能为空',
                        required: true,
                        trigger: 'blur'
                    },
                    {
                        name: '手机号',
                        validator: vm.$validateApi.tel,
                        trigger: 'blur'
                    }
                ],
                code: [{
                    message: '验证码不能为空',
                    required: true,
                    trigger: 'blur'
                }, {
                    name: '验证码',
                    len: 6,
                    validator: vm.$validateApi.lenAndnumber,
                    trigger: 'blur'
                }],
                userid: [{
                    required: true,
                    message: '用户昵称不能为空',
                    trigger: 'blur'
                }, {
                    name: '用户昵称',
                    len: 6,
                    validator: vm.$validateApi.cnLength,
                    trigger: 'blur'
                }],
                pass: [{
                    required: true,
                    message: '密码不能为空',
                    trigger: 'blur'
                }, {
                    validator: vm.$validateApi.password,
                    trigger: 'blur'
                }],
                password: [{
                    value: '',
                    validator: vm.$validateApi.validateRepass,
                    trigger: 'blur'
                }]
            },
            codedis: false,
            codeinner: '获取验证码',
            errormsg: ''
        };
    },
    components:{
        inputPassword:()=>import('@/components/inputPassword.vue')
    },
    methods: {
        inputres(){
            var vm=this;
            vm.rules.yPass[0].value = vm.updateData.newpass;
        },
        loginCode() {
            let vm = this;
            let tel = vm.newuser.tel;
            if (vm.codedis) {
                vm.$message({
                    type: 'error',
                    message: '请等待一段时间后再试'
                })
            } else {
               vm.$axios.post('/sendCode.action',vm.utils.data.formatter({tel:tel}))
                    .then(function (response) {
                        let res = response;
                        if (res.bool) {
                            vm.codeDis();
                            vm.$message({
                                type: 'success',
                                duration: 1000,
                                message: '验证码已发送，请注意查收'
                            })
                        }
                    })
                    .catch(function (error) {
                        console.log(error)
                    });
            }
        },
        codeDis() {
            let vm = this;
            vm.codedis = true;
            let i = 10;
            upText();
            let times = setInterval(upText, 1000);
            function upText() {
                vm.codeinner = i + '秒后继续';
                i -= 1;
                if (i <= -1) {
                    clearInterval(times);
                    vm.codeinner = '未收到？再次获取';
                    vm.codedis = false;
                }
            }
        },
        submits() {
            let vm = this;
            let newuser = vm.newuser;
            vm.$refs['regform'].validate(bool => {
                if (bool) {
                    vm.$axios
                        .post('/register.action',vm.uitl.data.formatter(newuser))
                        .then(function (success) {
                            let res = success;
                            if (res.bool) {
                                vm.$message({
                                    type: 'success',
                                    duration: 800,
                                    message: '注册成功',
                                    onClose() {
                                        vm.$router.push('/');
                                    }
                                })
                            } else {
                                vm.$message({
                                    type: 'warning',
                                    duration: 800,
                                    message: res.msg
                                })
                            }
                        })
                        .catch(function (error) {
                            console.log(error)
                        }).finally(() => {
                            vm.loading = false;
                        });
                } else {
                    vm.loading = false;
                }
            })
        }
    }
};
</script>

<style scope="scoped">
.user_box.container{
    position: relative;
    top:10rem;
    width:30%;
    min-width:600px;
    margin: auto;
}
.register-form{
    border:1px solid rgba(230, 228, 123, 0.466);
    padding: 1.5rem;
    box-shadow:1rem 1rem 0.5rem rgba(178, 199, 61, 0.37);
  }
.user_box .panel-info{
    border: none;
}
</style>
